<template>
    <div class="row pt-4 pt-md-0 whiteBack">

        <div class="col-12 pt-4">

            <div class="row pt-4">
                <div class="col-11 col-md-4 col-lg-4 mx-auto">

                    <div class="auth-header">
                        <h2 class="font-weight-bold">Login</h2>
                        <p>Login into your goopho account.</p>
                    </div>

                    <form @submit.prevent="loginUser">
                    
                        <div class="form-group mb-4">
                            <input v-model="userData.username" class="form-control customInput" id="exampleInputEmail1" aria-describedby="emailHelp" placeholder="Enter Email Address">
                        </div>

                        <div class="form-group mb-4">
                            <input v-model="userData.password" type="password" class="form-control customInput" placeholder="Enter Password" id="exampleInputPassword1">
                        </div>

                        <div class="text-danger text-center mt-2 mb-4 p-3 border error" v-if="errors !== 200">
                            Wrong Username and Password combination
                        </div>

                        <button :disabled="loggin" type="submit" class="btn customInputButton">
                            <div v-show="!loggin">Sign in</div>
                            <span v-show="loggin" class="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                        </button>

                        <div class="mt-3 ">
                            <a href="#"><p class="font-weight-bold">Forgot Password?</p></a>
                        </div>
                        
                        <hr class="divider">

                    </form>

                    <div class="row justify-content-between">
                        <div class="col-12 col-md-6 mb-3 mb-md-0">
                            <button class="btn border p-3" style="width:100%;">
                                Sign in with Google
                            </button>
                        </div>

                        <div class="col-12 col-md-6">
                            <button class="btn border p-3" style="width:100%;">
                                Sign in with Facebook
                            </button>
                        </div>
                    </div>

                    <div class="text-center mt-4">
                        Not yet a member <router-link to="register"><a href="#">Create an account</a></router-link>
                    </div>

                </div>
            </div>
            
        </div>

    </div>
</template>

<script>
    export default {
        name: 'login',
        data(){
            return{
                userData:{
                    username:'',
                    password:'',
                },
                errors:200,
                loggin:false,
            }  
        },     
        methods: {
           loginUser:function(){
                this.loggin = true

                this.errors = 200

                this.$http.post('http://127.0.0.1:5000/login',{},{
                    auth:this.userData
                })
                .then((res) =>{
                    this.loggin = false
                    this.errors = res.status
                    //console.log(res.data)
                    this.$store.commit("loggedInSaveAuth",res.data)
                    this.$router.push("/")

                })
                .catch((err) => {
                   this.errors = err.response.status
                   this.loggin = false
                   this.$store.commit("errorLogginIn")
                   
                })

                //file
                //title
                //descri
                //upload


            },
        },  
    }
</script>


